@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-400.woff2") format("woff2"), url("../fonts/inter-400.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-500.woff2") format("woff2"), url("../fonts/inter-500.woff") format("woff");
}
@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-600.woff2") format("woff2"), url("../fonts/inter-600.woff") format("woff");
}
@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/inter-700.woff2") format("woff2"), url("../fonts/inter-700.woff") format("woff");
}
body {
  margin: 0;
  padding: 0;
  font-family: "inter", "Arial", sans-serif;
  background-color: #f5f7fa;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-header__logo {
  display: flex;
  flex-wrap: wrap;
  column-gap: 10px;
  align-items: center;
  padding: 32px 24px 32px 43px;
}
@media (max-width: 767px) {
  .main-header__logo {
    display: none;
  }
}

.main-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
}
@media (max-width: 767px) {
  .main-header__wrapper {
    padding: 16px;
    padding-bottom: 18px;
  }
}

.main-header__wrapper .main-header__title {
  font-weight: 600;
  font-size: 28px;
  line-height: 34px;
  font-family: "inter", "Arial", sans-serif;
  margin: 0;
  margin-left: 20px;
  margin-top: 10px;
  color: #343c6a;
}
@media (max-width: 1023px) {
  .main-header__wrapper .main-header__title {
    font-weight: 600;
    font-size: 26px;
    line-height: 34px;
  }
}
@media (max-width: 767px) {
  .main-header__wrapper .main-header__title {
    font-size: calc(20px + 17 * (100vw - 320px) / 1440);
    grid-row: 1;
    grid-column: 2;
    margin-top: 10px;
    margin-left: 13px;
  }
}

.main-header__info {
  border-left: 1px solid #e6eff5;
  display: flex;
  flex-wrap: wrap;
  padding: 24px 22px;
  padding-bottom: 15px;
  flex: 1;
  justify-content: space-between;
}
@media (max-width: 899px) {
  .main-header__info {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 5px;
  }
}
@media (max-width: 767px) {
  .main-header__info {
    border-left: none;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto;
    padding: 0;
    row-gap: 15px;
  }
}

.search__field {
  position: relative;
}
@media (max-width: 767px) {
  .search__field {
    margin-left: 8px;
    margin-right: 8px;
  }
}

.search__icon {
  position: absolute;
  top: 17px;
  left: 24px;
}
@media (max-width: 767px) {
  .search__icon {
    width: 15px;
    height: 15px;
    top: 14px;
    left: 20px;
  }
}

.search__input {
  border-radius: 40px;
  background: #f5f7fa;
  outline: none;
  border: none;
  padding: 13px 8px 13px 52px;
  font-weight: 400;
  font-size: 15px;
  line-height: 26px;
  font-family: "inter", "Arial", sans-serif;
}
@media (max-width: 767px) {
  .search__input {
    width: 100%;
    padding: 9px 8px 8px 45px;
  }
}
.search__input::placeholder {
  color: #8ba3cb;
  margin-right: 10px;
  font-family: "inter", "Arial", sans-serif;
}

.main-header__setting {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background: #f5f7fa;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
}
@media (max-width: 899px) {
  .main-header__setting {
    justify-self: end;
  }
}
@media (max-width: 767px) {
  .main-header__setting {
    display: none;
  }
}

.main-header__not {
  width: 50px;
  height: 50px;
  border-radius: 100%;
  background: #f5f7fa;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  outline: none;
}
@media (max-width: 899px) {
  .main-header__not {
    justify-self: end;
  }
}
@media (max-width: 767px) {
  .main-header__not {
    display: none;
  }
}

.main-header__link {
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  text-decoration: none;
  font-family: "inter", "Arial", sans-serif;
  text-transform: uppercase;
  color: #ffffff;
}
@media (max-width: 1439px) {
  .main-header__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: #000000;
  }
}
@media (max-width: 767px) {
  .main-header__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    line-height: 133%;
    text-align: center;
    color: #000000;
  }
}
@media (max-width: 320px) {
  .main-header__link {
    font-weight: 500;
    font-size: 20px;
    line-height: 26px;
    line-height: 100%;
    text-align: center;
    color: #000000;
  }
}

.main-header__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 34px;
}
@media (max-width: 1439px) {
  .main-header__list {
    margin-top: 5px;
    gap: 36px;
  }
}
@media (max-width: 767px) {
  .main-header__list {
    gap: 0;
  }
}

@media (max-width: 767px) {
  .main-header__item {
    width: 100%;
    text-align: center;
    border-top: 1px solid #e6e6e6;
    padding-top: 10px;
    padding-bottom: 27px;
  }
}

.main-header__item--active {
  border-bottom: 2px solid #68b738;
}
@media (max-width: 767px) {
  .main-header__item--active {
    border: none;
  }
}

.main-header__link--index-active {
  border-bottom: 2px solid #ffffff;
  padding-bottom: 5px;
}
@media (max-width: 1439px) {
  .main-header__link--index-active {
    border-bottom: 2px solid #68b738;
  }
}
@media (max-width: 767px) {
  .main-header__link--index-active {
    border: none;
  }
}

.main-header__search {
  grid-column: 1/4;
}

.main-header__image {
  margin-right: 18px;
  margin-top: -4px;
}
@media (max-width: 899px) {
  .main-header__image {
    margin: 0;
  }
}
@media (max-width: 767px) {
  .main-header__image {
    grid-row: 1;
    grid-column: 3;
  }
  .main-header__image picture {
    float: right;
    margin-top: 9px;
    margin-right: 10px;
  }
}
.main-header__image img {
  border-radius: 50%;
}

.main-header__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-header__toggle {
    position: absolute;
    top: 0;
    right: 0;
    width: 60px;
    height: 50px;
    background-color: #ffffff;
    cursor: pointer;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width: 767px) {
  .main-header__l {
    position: absolute;
    width: 100%;
    left: 0;
    background-color: #ffffff;
  }
}

@media (max-width: 767px) {
  .main-header__wrapper--nojs .main-header__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-header__wrapper--nojs .main-header__list {
    display: block;
  }
}

.main__wrapper {
  padding: 31px 40px 10px 44px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .main__wrapper {
    width: 100%;
  }
}
.main__wrapper ul {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0;
  margin: 0;
  font-weight: 500;
  font-size: 19px;
  line-height: 18px;
  font-family: "inter", "Arial", sans-serif;
  row-gap: 36px;
}
.main__wrapper li {
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-start;
}
.main__wrapper a {
  text-decoration: none;
  color: #b1b1b1;
  fill: #b1b1b1;
  stroke: #b1b1b1;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  column-gap: 27px;
}
.main__wrapper a:hover {
  color: #1814f3;
  fill: #1814f3;
  stroke: #1814f3;
}

.main-header {
  display: flex;
  flex-direction: column;
  background-color: #ffffff;
}

.main {
  display: flex;
  flex-wrap: wrap;
  min-height: calc(100vh - 100px);
}

.main-header__subscribe {
  display: flex;
  flex-wrap: wrap;
  column-gap: 32px;
}
@media (max-width: 1023px) {
  .main-header__subscribe {
    column-gap: 10px;
  }
}
@media (max-width: 899px) {
  .main-header__subscribe {
    row-gap: 5px;
    justify-content: end;
  }
}
@media (max-width: 767px) {
  .main-header__subscribe {
    width: auto;
    display: contents;
  }
}
.main-header__subscribe h1 {
  font-weight: 400;
  font-size: 60px;
  line-height: 26px;
  line-height: 100%;
  color: #000000;
  padding: 0;
  margin: 0;
  margin-top: 106px;
  margin-bottom: 40px;
}
@media (max-width: 1439px) {
  .main-header__subscribe h1 {
    line-height: 100%;
    color: #000000;
    margin-top: 59px;
    margin-bottom: 41px;
  }
}
@media (max-width: 767px) {
  .main-header__subscribe h1 {
    font-weight: 400;
    font-size: 36px;
    line-height: 26px;
    line-height: 100%;
    text-align: center;
    color: #ffffff;
    margin-top: 26px;
    margin-bottom: 26px;
  }
}
.main-header__subscribe p {
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  line-height: 100%;
  color: #000000;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  margin-bottom: 52px;
}
@media (max-width: 1439px) {
  .main-header__subscribe p {
    margin-left: 4px;
    margin-bottom: 49px;
  }
}
@media (max-width: 767px) {
  .main-header__subscribe p {
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    line-height: 100%;
    color: #ffffff;
    margin-left: -5px;
    margin-bottom: 28px;
  }
}
.main-header__subscribe a {
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  display: block;
  line-height: 100%;
  padding: 17px 27px;
  margin: 0;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  background-color: #68b738;
}
@media (max-width: 1439px) {
  .main-header__subscribe a {
    margin-left: 5px;
  }
}
@media (max-width: 767px) {
  .main-header__subscribe a {
    font-weight: 400;
    font-size: 14px;
    line-height: 26px;
    line-height: 129%;
    color: #ffffff;
    margin-left: 16px;
    margin-right: 19px;
    padding: 11px 27px;
  }
}

@media (max-width: 767px) {
  .main--closed .main__button::before {
    position: absolute;
    content: "";
    width: 17px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 6px 0 0 #000000, 0 12px 0 0 #000000;
  }
}

.main--open .main__button::before {
  position: absolute;
  content: "";
  cursor: pointer;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(45deg);
  box-shadow: none;
}

.main--open .main__button::after {
  position: absolute;
  content: "";
  cursor: pointer;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: rotate(-45deg);
}

@media (max-width: 767px) {
  .main--closed .main__wrapper {
    display: none;
  }
}
@media (max-width: 767px) {
  .main--open .main__wrapper {
    display: block;
  }
}
@media (max-width: 767px) {
  .main--nojs .main__wrapper {
    display: block;
  }
}
.main__button {
  display: none;
}
@media (max-width: 767px) {
  .main__button {
    grid-row: 1;
    width: 37px;
    height: 37px;
    background-color: #ffffff;
    cursor: pointer;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 20px;
    left: 20px;
  }
}

.main__item--active a {
  color: #1814f3;
  fill: #1814f3;
  stroke: #1814f3;
}

.profile {
  padding: 39px 31px 30px 26px;
  padding-top: 39px;
  background-color: #ffffff;
  margin-left: 41px;
  margin-top: 31px;
  margin-right: 42px;
  border-radius: 25px;
  height: fit-content;
  flex: 1;
}
@media (max-width: 767px) {
  .profile {
    margin: 25px;
    padding: 20px;
    padding-top: 22px;
    border-radius: 15px;
  }
}

.profile-content {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
@media (max-width: 1439px) {
  .profile-content {
    justify-content: flex-start;
  }
}
@media (max-width: 767px) {
  .profile-content {
    justify-content: center;
  }
}

.profile__form {
  display: grid;
  width: 82%;
  grid-template-columns: 48.5% 48.5%;
  column-gap: 29px;
  margin-bottom: 6px;
}
@media (max-width: 1439px) {
  .profile__form {
    width: 83%;
  }
}
@media (max-width: 1199px) {
  .profile__form {
    width: 100%;
  }
}
@media (max-width: 899px) {
  .profile__form {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }
}
@media (max-width: 767px) {
  .profile__form {
    margin-bottom: 3px;
  }
}

.profile__group {
  display: flex;
  flex-direction: column;
  border: none;
  padding: 0;
  margin: 0;
}

.profile__tab {
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  font-family: "inter", "Arial", sans-serif;
  background-color: #ffffff;
  color: #718ebf;
  border: none;
  text-transform: none;
  position: relative;
  padding: 0;
  letter-spacing: 0.03em;
}
@media (max-width: 767px) {
  .profile__tab {
    font-size: calc(13px + 4 * (100vw - 320px) / 1440);
  }
}
.profile__tab.active {
  color: #1814f3;
}

.profile__tab.active::after {
  position: absolute;
  content: "";
  width: calc(100% + 28px);
  height: 2px;
  left: -15px;
  bottom: -9px;
  background-color: #1814f3;
}
@media (max-width: 767px) {
  .profile__tab.active::after {
    width: calc(100% + 12px);
    left: -5px;
    bottom: -6px;
  }
}

.profile__tabs {
  position: relative;
  margin-bottom: 53px;
  padding-bottom: 10px;
  margin-left: 19px;
  display: flex;
  flex-wrap: wrap;
  column-gap: 72px;
}
@media (max-width: 899px) {
  .profile__tabs {
    margin-left: 0;
    column-gap: normal;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .profile__tabs {
    margin-left: 5px;
    padding-bottom: 7px;
    margin-bottom: 25px;
  }
}
.profile__tabs::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  background-color: #f4f5f7;
  width: 100%;
  height: 1px;
}
@media (max-width: 767px) {
  .profile__tabs::after {
    left: -5px;
  }
}

.profile-content.active-content {
  display: flex;
  flex-wrap: wrap;
}

.profile-picture {
  position: relative;
  height: fit-content;
  margin-right: 53px;
  margin-top: 2px;
}
@media (max-width: 767px) {
  .profile-picture {
    margin-right: 5px;
    margin-top: 0;
    margin-bottom: 18px;
  }
}

.profile__icon {
  position: absolute;
  content: "";
  bottom: 19px;
  right: 0;
  width: 30px;
  height: 30px;
  background-color: #1814f3;
  outline: none;
  border-radius: 50%;
  border: none;
  cursor: pointer;
}
@media (max-width: 767px) {
  .profile__icon {
    width: 38.6px;
    height: 38.6px;
    bottom: 24px;
    right: -5px;
  }
}

.profile__label {
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  font-family: "inter", "Arial", sans-serif;
  color: #232323;
  letter-spacing: 0.03em;
  margin-bottom: 12px;
}
@media (max-width: 767px) {
  .profile__label {
    font-weight: 400;
    font-size: 13px;
    line-height: 18px;
    margin-bottom: 7px;
  }
}

.profile__input {
  border: 1px solid #dfeaf2;
  border-radius: 15px;
  padding: 16px 16px 16px 20px;
  margin-bottom: 23px;
  letter-spacing: 0.01em;
}
@media (max-width: 767px) {
  .profile__input {
    border-radius: 10px;
    padding: 12px 16px 11px 14px;
    margin-bottom: 16px;
  }
}
.profile__input::placeholder {
  font-weight: 400;
  font-size: 15px;
  line-height: 18px;
  font-family: "inter", "Arial", sans-serif;
  color: #718ebf;
}
@media (max-width: 767px) {
  .profile__input::placeholder {
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
  }
}

.profile__button {
  font-weight: 500;
  font-size: 18px;
  line-height: 18px;
  font-family: "inter", "Arial", sans-serif;
  color: #ffffff;
  text-align: center;
  padding: 18px 72px 15px 72px;
  border-radius: 15px;
  background-color: #1814f3;
  border: none;
  cursor: pointer;
  height: fit-content;
}
@media (max-width: 767px) {
  .profile__button {
    width: 100%;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    border-radius: 9px;
    padding: 11px 72px 11px 72px;
  }
}

.profile__wrap {
  display: flex;
  flex-direction: column;
  border: 0;
  padding: 0;
  margin: 0;
}

@media (max-width: 767px) {
  /* Убираем эффект группировки fieldset */
  .profile__group {
    display: contents;
  }

  /* Изменяем порядок элементов */
  .profile__wrap:nth-child(1) {
    order: 1;
  }

  /* Your Name */
  .profile__wrap:nth-child(6) {
    order: 2;
  }

  /* User Name */
  .profile__wrap:nth-child(2) {
    order: 3;
  }

  /* Email */
  .profile__wrap:nth-child(7) {
    order: 4;
  }

  /* Password */
  .profile__wrap:nth-child(3) {
    order: 5;
  }

  /* Date of Birth */
  .profile__wrap:nth-child(8) {
    order: 6;
  }

  /* Present Address */
  .profile__wrap:nth-child(4) {
    order: 7;
  }

  /* Permanent Address */
  .profile__wrap:nth-child(9) {
    order: 8;
  }

  /* City */
  .profile__wrap:nth-child(5) {
    order: 9;
  }

  /* Postal Code */
  .profile__wrap:nth-child(10) {
    order: 10;
  }
}
.tab-content {
  display: none;
}

.active-content {
  display: block;
}